      let list =[skills,projects,about]
      window.addEventListener('scroll', function() {
      for(let i = 0;i<list.length;i++) {
          if(list[i].getBoundingClientRect().top <= 75 && list[i].getBoundingClientRect().bottom > 100 && (list[i].getBoundingClientRect().top > -(list[i].getBoundingClientRect().bottom - list[i].getBoundingClientRect().top))/2) {
              list.map((elm,i)=>{document.querySelector('.fixed__category').children[i].classList.remove('active');
              console.log(i)
              })
              document.querySelector('.fixed__category').children[i].classList.add('active')
          }
      }
      })
    
    },
  },
  mounted() {
    this.scrollListen()
  },
}
